import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Calendar from '@/components/Calendar'
import Portfolio from '@/components/Portfolio'
import Contact from '@/components/Contact'
import Redirect from '@/components/Redirect'
import ErrorPage from '@/components/_404'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/calendar',
      name: 'Calendar',
      component: Calendar
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/redirect',
      name: 'Redirect',
      component: Redirect
    },
    {
      path: '/404',
      name: '404',
      component: ErrorPage
    },
    {
      path: '*',
      redirect: to => {
        // Handle dynamic redirects
        /* eslint-disable */
        var redirects = _redirects !== undefined ? _redirects : false
        if (redirects) {
          for (var i = 0; i < redirects.length; i++) {
            if (to.path.toLowerCase() === redirects[i].path.toLowerCase()) {
              window.location.replace(redirects[i].redirect)
              return 'redirect'
            }
          }
        }
        return '/404'
        /* eslint-enable */
      }
    }
  ]
})



